<template>
  <div :class="!isMobile ? 'pt-3' : 'pt-0'" style="max-width:1300px;height: 100%; background-color: #ffff;" >
    <v-layout row wrap>
      <v-flex xs12 md9 v-if="votingItems.length > 0">
        <v-layout row wrap>
